import React from 'react';
import axios from "axios";
import Movie from "./Movie";
import "./App.css";

class App extends React.Component{
  state = {
    isLoading: true,
    movies: []
  };

  // axios는 느리다 -> didmount 함수가 끝날때 까지 기다려야함
  // 이 함수는 비동기, await로 axios를 기다려라고 지정
  getMovies = async () => {
    // es6를 통해 movies.data.data.movies 축약하기
    const {
      data: {
        data: { movies }
      }
    } = await axios.get("https://yts-proxy.now.sh/list_movies.json?sort_by=rating");
    console.log(movies);
    // es6로 { movies : movies } 를 축약
    this.setState({ movies, isLoading: false });
    // loading이 끝나면 isLoading을 false로
  }

  componentDidMount() {
    /*
    // 3초후 We are Ready로 변경
    setTimeout(() => {
      this.setState({ isLoading: false });
    }, 3000);
    */

    // 보통 data 를 fetch() 하지만 axios는 fetch 위의 레이어
    this.getMovies();
  }

  render() {
    const { isLoading, movies } = this.state;
    // JSX에서는 일반 class와 jsx class명을 혼동하지 않기 위해
    // class 대신 className을 사용한다.
    return (
      <section className="container">
        {isLoading ? (
          <div className="loader">
            <span className="loader_text">Loading...</span>
          </div>
        ) : (
          <div className="movies">
            {movies.map(movie => (
              <Movie
                key={movie.id}
                id={movie.title}
                year={movie.year}
                title={movie.title}
                summary={movie.summary}
                poster={movie.medium_cover_image}
                genres={movie.genres}
              />
            ))}
          </div>
        )}
      </section>
    );
  }
}

export default App;
